@import url(https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,300;8..144,400;8..144,600;8..144,700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Flex', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Roboto Flex', sans-serif;
}

.logoNav { 
    text-decoration: none;
    overflow: hidden;
    float: left;
    margin-left: 5%;
    margin-top: 0.5%;
    font-size: 150%;
    color: white;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 1000;
    font-style: italic;
}
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgb(0, 0, 0);
}

li {
    float: right;
}

li a {
    display: flex;
    color: white;
    text-align: center;
    padding: 16px 50px;
    text-decoration: none;
}

li a:hover {
    background-color: #111;
    
}

.active {
    background-color: #6C63FF;
}

.corFundo {
    height: 520px;
    background-image: linear-gradient(180deg, #cccbf8 0%, rgba(133, 127, 244, 0) 77.87%);
    display: flex;
}

.containerMensagens{
    display: flex;
}

.imagemInicial {

    width: 450px;
    height: 450px;
}
.msgInicio {
    
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-style: normal;
    font-weight: 700;   
    font-size: 230%;
    line-height: 37px;
    padding-top: 16%;
    padding-left: 8%;
    padding-right: 3%;
    margin-top: 0;
    border: 0;
    
}

.msgInicio2 {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    font-size: 100%;  
    padding-top: 1%;
    padding-left: 8%;
    padding-right: 26%;
    
}

.inserirEmail {
    
    width: 47%;
    padding: 1.6% 1%;
    margin-top: 3.7%;
    margin-left: 8%;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;

}

.inserirEmail::-webkit-input-placeholder {
    color: rgb(0, 0, 0);
  
}

.inserirEmail::placeholder {
    color: rgb(0, 0, 0);
  
}

.botaoCriar {
    width: 28%;
    background-color: #000000;
    color: white;
    padding: 14px 20px;
    margin-left: 1%;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

.campoEbotao {
    display: block;
}
.categoriasFundo {
    display: flex;
    background-color: #E6E7FF;
    padding-top: 2.7%;
    padding-bottom: 2.7%;

}

.categoriasPassagem{
    background-color: #E6E7FF;
    color: rgb(0, 0, 0);
    list-style-type: none;
    overflow: hidden;
    margin-left: 20%;
    display: flex;
}


.categoriasPassagem li{
    text-align: center;
    padding: 0px 30px 0px 10px;
       
}
.titulo1 {
    margin: 5%;
    text-align: center;
    font-size: 210%;
}

.blocoInfos {
    display: flex;
    margin-bottom: 1%;
}

.imagemEsquerda {
    padding-left: 5%;
}

.imagemDireita {
    padding-left: 8%;
}



.descricaoEsquerda{
    margin-left: 10%;
}

h5 {
    font-weight: 450;
    font-size: 100%;
}
.gridMain{
    display: grid;
    grid-template-columns: 1.2fr 0.8fr;
    grid-gap: 1rem;
    gap: 1rem;
    width: 100%;
    box-sizing: border-box;
    padding: 1rem 5rem;
}
.section{
    padding: 0rem 10rem;
}
.formulario{
    width: 100%;
    max-width: 46rem;
}
.input-duplo{
    display: grid;
    grid-gap: 1.6rem;
    gap: 1.6rem;
    grid-auto-flow: column;
    padding-top: 1rem;
}
.input-single{
    display: block;
    position: relative;
    padding-top: 1rem;
    width: 100%;
}
.background{
    background: linear-gradient(to top, rgb(108, 99, 255), rgb(108, 60, 167));
    position: relative;
    border-radius: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.botao-row{
    display: flex;
    padding-top: 1.6rem;
    justify-content: space-between;

}
.aside{
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    height: 100vh;
}


.Header {
    display:flex;
    background: black;
    color: white;
    height: 60px;
    justify-content: space-between;
}

.Header h1{
    margin: 0;
    padding: 10px;
    line-height: 40px;
}
.Opcao{
    display: flex;
    flex-direction: row;
    grid-gap: 25px;
    gap: 25px;
    line-height: 60px;
}

.foto {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background: #ccc;
    text-align: center;
    line-height: 45px;
    align-self: flex-start;
}

.SubHeader{
    background: #342F86;
    height: 70px;
    color: white;
    line-height: 50px;
    padding: 10px;
}
.SubHeader h2{
    margin: 0;
}
